import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { MatInput } from '@angular/material/input';

import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { MAT_KEYBOARD_DEADKEYS } from '../../configs/keyboard-deadkey.config';
import { MAT_KEYBOARD_ICONS } from '../../configs/keyboard-icons.config';
import { KeyboardClassKey } from '../../enums/keyboard-class-key.enum';

export const VALUE_NEWLINE = '\n\r';
export const VALUE_SPACE = ' ';
export const VALUE_TAB = '\t';

@Component({
  selector: 'mat-keyboard-key',
  template: `
    <button mat-raised-button
            class="mat-keyboard-key"
            tabindex="-1"
            [class.mat-keyboard-key-active]="active$ | async"
            [class.mat-keyboard-key-pressed]="pressed$ | async"
            [ngClass]="cssClass"
            (click)="onClick()"
    >
      <mat-icon *ngIf="hasIcon">{{ icon }}</mat-icon>
      <ng-container *ngIf="!hasIcon">{{ key }}</ng-container>
    </button>
  `,
  styles: [`
    @charset "UTF-8";
    /**
     * Applies styles for users in high contrast mode. Note that this only applies
     * to Microsoft browsers. Chrome can be included by checking for the \`html[hc]\`
     * attribute, however Chrome handles high contrast differently.
     */
    /* Theme for the ripple elements.*/
    /** The mixins below are shared between mat-menu and mat-select */
    /**
     * This mixin adds the correct panel transform styles based
     * on the direction that the menu panel opens.
     */
    /* stylelint-disable material/no-prefixes */
    /* stylelint-enable */
    /**
     * This mixin contains shared option styles between the select and
     * autocomplete components.
     */
    :host {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-family: Roboto, "Helvetica Neue", sans-serif;
      font-size: 14px;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      line-height: 20px; }

    .mat-keyboard-key {
      min-width: 0;
      width: 100%; }
      .mat-keyboard-key-active {
        background-color: #e0e0e0; }
      .mat-keyboard-key-pressed {
        background-color: #bdbdbd; }
      .mat-keyboard-key-capslock {
        background-color: white; }
        .mat-keyboard-key-capslock:before {
          background-color: #bdbdbd;
          border-radius: 100%;
          content: '';
          display: inline-block;
          height: 3px;
          left: 5px;
          position: absolute;
          top: 5px;
          -webkit-transition: 400ms cubic-bezier(0.25, 0.8, 0.25, 1);
          transition: 400ms cubic-bezier(0.25, 0.8, 0.25, 1);
          -webkit-transition-property: background-color, -webkit-box-shadow;
          transition-property: background-color, -webkit-box-shadow;
          transition-property: background-color, box-shadow;
          transition-property: background-color, box-shadow, -webkit-box-shadow;
          width: 3px; }
        .mat-keyboard-key-capslock.mat-keyboard-key-active:before {
          background-color: #0f0;
          -webkit-box-shadow: 0 0 §px #adff2f;
                  box-shadow: 0 0 §px #adff2f; }

    :host-context(.dark-theme) .mat-keyboard-key {
      background-color: #616161;
      color: whitesmoke; }
      :host-context(.dark-theme) .mat-keyboard-key-active {
        background-color: #9e9e9e; }
      :host-context(.dark-theme) .mat-keyboard-key-pressed {
        background-color: #757575; }

    :host-context(.debug) .mat-keyboard-key-deadkey {
      background-color: cadetblue; }

    :host-context(.debug) .mat-keyboard-key-deadkey.mat-keyboard-key-active {
      background-color: #6fa8aa; }

    :host-context(.debug) .mat-keyboard-key-deadkey.mat-keyboard-key-pressed {
      background-color: #7fb1b3; }

    :host-context(.debug) .mat-keyboard-key-modifier {
      background-color: aquamarine; }

    :host-context(.debug) .mat-keyboard-key-modifier.mat-keyboard-key-active {
      background-color: #99ffdd; }

    :host-context(.debug) .mat-keyboard-key-modifier.mat-keyboard-key-pressed {
      background-color: #b2ffe5; }

    :host-context(.dark-theme.debug) .mat-keyboard-key-deadkey {
      background-color: rebeccapurple; }

    :host-context(.dark-theme.debug) .mat-keyboard-key-deadkey.mat-keyboard-key-active {
      background-color: #7339ac; }

    :host-context(.dark-theme.debug) .mat-keyboard-key-deadkey.mat-keyboard-key-pressed {
      background-color: #8040bf; }

    :host-context(.dark-theme.debug) .mat-keyboard-key-modifier {
      background-color: mediumpurple; }

    :host-context(.dark-theme.debug) .mat-keyboard-key-modifier.mat-keyboard-key-active {
      background-color: #a284e0; }

    :host-context(.dark-theme.debug) .mat-keyboard-key-modifier.mat-keyboard-key-pressed {
      background-color: #b299e5; }
  `],
  changeDetection: ChangeDetectionStrategy.OnPush,
  preserveWhitespaces: false
})
export class MatKeyboardKeyComponent implements OnInit {

  private _deadkeyKeys: string[] = [];

  private _iconKeys: string[] = [];

  active$: BehaviorSubject<boolean> = new BehaviorSubject(false);

  pressed$: BehaviorSubject<boolean> = new BehaviorSubject(false);

  @Input()
  key: string | KeyboardClassKey;

  @Input()
  set active(active: boolean) {
    this.active$.next(active);
  }

  get active(): boolean {
    return this.active$.getValue();
  }

  @Input()
  set pressed(pressed: boolean) {
    this.pressed$.next(pressed);
  }

  get pressed(): boolean {
    return this.pressed$.getValue();
  }

  @Input()
  input?: ElementRef;

  @Input()
  control?: MatInput;

  @Output()
  enterClick = new EventEmitter<void>();

  @Output()
  capsClick = new EventEmitter<void>();

  @Output()
  altClick = new EventEmitter<void>();

  @Output()
  shiftClick = new EventEmitter<void>();

  get lowerKey(): string {
    return `${this.key}`.toLowerCase();
  }

  get charCode(): number {
    return `${this.key}`.charCodeAt(0);
  }

  get isClassKey(): boolean {
    return this.key in KeyboardClassKey;
  }

  get isDeadKey(): boolean {
    return this._deadkeyKeys.some((deadKey: string) => deadKey === `${this.key}`);
  }

  get hasIcon(): boolean {
    return this._iconKeys.some((iconKey: string) => iconKey === `${this.key}`);
  }

  get icon(): string {
    return this._icons[this.key];
  }

  get cssClass(): string {
    const classes = [];

    if (this.hasIcon) {
      classes.push('mat-keyboard-key-modifier');
      classes.push(`mat-keyboard-key-${this.lowerKey}`);
    }

    if (this.isDeadKey) {
      classes.push('mat-keyboard-key-deadkey');
    }

    return classes.join(' ');
  }

  get inputValue(): string {
    if (this.control) {
      return this.control.value;
    } else if (this.input && this.input.nativeElement && this.input.nativeElement.value) {
      return this.input.nativeElement.value;
    } else {
      return '';
    }
  }

  set inputValue(inputValue: string) {
    if (this.control) {
      this.control.value = inputValue;
    } else if (this.input) {
      this.input.nativeElement.value = inputValue;
    }
  }

  // Inject dependencies
  constructor(@Inject(MAT_KEYBOARD_DEADKEYS) private _deadkeys,
              @Inject(MAT_KEYBOARD_ICONS) private _icons) {}

  ngOnInit() {
    // read the deadkeys
    this._deadkeyKeys = Object.keys(this._deadkeys);

    // read the icons
    this._iconKeys = Object.keys(this._icons);
  }

  onClick() {
    // Trigger a global key event
    // TODO: determine whether an output should bubble the pressed key similar to the keybboard action or not
    this._triggerKeyEvent();

    // Manipulate the focused input / textarea value
    const value = this.inputValue;
    const caret = this.input ? this._getCursorPosition() : 0;

    let char: string;
    switch (this.key) {
      // this keys have no actions yet
      // TODO: add deadkeys and modifiers
      case KeyboardClassKey.Alt:
      case KeyboardClassKey.AltGr:
      case KeyboardClassKey.AltLk:
        this.altClick.next();
        break;

      case KeyboardClassKey.Bksp:
        this.inputValue = [value.slice(0, caret - 1), value.slice(caret)].join('');
        this._setCursorPosition(caret - 1);
        break;

      case KeyboardClassKey.Caps:
        this.capsClick.next();
        break;

      case KeyboardClassKey.Enter:
        if (this._isTextarea()) {
          char = VALUE_NEWLINE;
        } else {
          this.enterClick.next();
          // TODO: trigger submit / onSubmit / ngSubmit properly (for the time being this has to be handled by the user himself)
          // console.log(this.control.ngControl.control.root)
          // this.input.nativeElement.form.submit();
        }
        break;

      case KeyboardClassKey.Shift:
        this.shiftClick.next();
        break;

      case KeyboardClassKey.Space:
        char = VALUE_SPACE;
        break;

      case KeyboardClassKey.Tab:
        char = VALUE_TAB;
        break;

      default:
        // the key is not mapped or a string
        char = `${this.key}`;
        break;
    }

    if (char && this.input) {
      this.inputValue = [value.slice(0, caret), char, value.slice(caret)].join('');
      this._setCursorPosition(caret + 1);
    }
  }

  private _triggerKeyEvent(): Event {
    const keyboardEvent = new KeyboardEvent('keydown');
    
//    keyboardEvent[initMethod](
//      true, // bubbles
//      true, // cancelable
//      window, // viewArg: should be window
//      false, // ctrlKeyArg
//      false, // altKeyArg
//      false, // shiftKeyArg
//      false, // metaKeyArg
//      this.charCode, // keyCodeArg : unsigned long - the virtual key code, else 0
//      0 // charCodeArgs : unsigned long - the Unicode character associated with the depressed key, else 0
//    );

    window.dispatchEvent(keyboardEvent);

    return keyboardEvent;
  }

  // inspired by:
  // ref https://stackoverflow.com/a/2897510/1146207
  private _getCursorPosition(): number {
    if (!this.input) {
      return;
    }

    if ('selectionStart' in this.input.nativeElement) {
      // Standard-compliant browsers
      return this.input.nativeElement.selectionStart;
    } else if (window.document['selection']) {
      // IE
      this.input.nativeElement.focus();
      const sel = window.document['selection'].createRange();
      const selLen = window.document['selection'].createRange().text.length;
      sel.moveStart('character', -this.control.value.length);

      return sel.text.length - selLen;
    }
  }

  // inspired by:
  // ref https://stackoverflow.com/a/12518737/1146207
  // tslint:disable one-line
  private _setCursorPosition(position: number): boolean {
    if (!this.input) {
      return;
    }

    this.inputValue = this.control.value;
    // ^ this is used to not only get "focus", but
    // to make sure we don't have it everything -selected-
    // (it causes an issue in chrome, and having it doesn't hurt any other browser)

    if ('createTextRange' in this.input.nativeElement) {
      const range = this.input.nativeElement.createTextRange();
      range.move('character', position);
      range.select();
      return true;
    } else {
      // (el.selectionStart === 0 added for Firefox bug)
      if (this.input.nativeElement.selectionStart || this.input.nativeElement.selectionStart === 0) {
        this.input.nativeElement.focus();
        this.input.nativeElement.setSelectionRange(position, position);
        return true;
      }
      // fail city, fortunately this never happens (as far as I've tested) :)
      else {
        this.input.nativeElement.focus();
        return false;
      }
    }
  }

  private _isTextarea(): boolean {
    return this.input && this.input.nativeElement && this.input.nativeElement.tagName === 'TEXTAREA';
  }

}
